import 'semantic-ui-css/semantic.min.css'

import { useState, useEffect } from 'react';
import { createClient } from "@supabase/supabase-js";
import { Auth } from '@supabase/auth-ui-react'
import { ThemeSupa } from '@supabase/auth-ui-shared'
import {
  TableRow,
  TableHeaderCell,
  TableHeader,
  TableCell,
  TableBody,
  Flag,
  Icon,
  Table,
  Rating,
  Image
} from 'semantic-ui-react'


const supabase = createClient('https://fnicvjrmmtfaqywwhdwy.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImZuaWN2anJtbXRmYXF5d3doZHd5Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzM3MDY2NjgsImV4cCI6MjA0OTI4MjY2OH0.St4GG0BWQfQlojlbGimBQyRagkl3FN73CyYsgeMa5bs')

export default function News() {

  const [session, setSession] = useState(null)
  const [events, setEvents] = useState([]);

  useEffect(() => {
    getEvents();
  }, []);

  async function getEvents() {
    var date = new Date();
    date.setDate(date.getDate() - 3);
    const { data } = await supabase.from("economic-news").select().gt('time', date.toJSON()).order('time', { ascending: false });
    let mainEvets = [];
    for (const e of data){
      let obj = {...e};
      obj.importance = obj.impact == '' ? -1 : (obj.impact == '' ? 0 : 1);
      mainEvets.push(obj)
    }
    setEvents(mainEvets);
  }

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
    })

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session)
    })

    return () => subscription.unsubscribe()
  }, [])

  if (!session) {
    return (<Auth supabaseClient={supabase} appearance={{ theme: ThemeSupa }} />)
  }
  else {
    return (
      <Table celled>
      <TableHeader>
        <TableRow>
          <TableHeaderCell>id</TableHeaderCell>
          <TableHeaderCell>title</TableHeaderCell>
          <TableHeaderCell>(tokyo)</TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
        </TableRow>
      </TableHeader>
      <TableBody>
        {events.map((event) => (
          <TableRow key={event.id}
            positive={event.sentiment === ""}
            negative={event.sentiment === ""}
          >
            <TableCell>
            
            <a href={event.url} target="_blank">{event.id}</a>
            </TableCell>
            <TableCell>
            {event.content}
            </TableCell>
            <TableCell>
            {new Date(event.time).toLocaleDateString('ja-JP',{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric',
            })}
            </TableCell>
            <TableCell>
            {event.sentiment}
            </TableCell>
            <TableCell>
            {event.confidence}
            </TableCell>
            <TableCell>
            <Rating icon='star' defaultRating={event.importance + 2}  maxRating={event.importance + 2} disabled/>
            </TableCell>
            <TableCell>
            {event.reason}
            </TableCell>
          </TableRow>
        ))}
        </TableBody>
      </Table>
    );
  }
}