import 'semantic-ui-css/semantic.min.css'

import { useState, useEffect } from 'react';
import { createClient } from "@supabase/supabase-js";
import { Auth } from '@supabase/auth-ui-react'
import { ThemeSupa } from '@supabase/auth-ui-shared'
import {
  TableRow,
  TableHeaderCell,
  TableHeader,
  TableCell,
  TableBody,
  Flag,
  Icon,
  Table,
  Rating,
  Image
} from 'semantic-ui-react'


const supabase = createClient('https://fnicvjrmmtfaqywwhdwy.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImZuaWN2anJtbXRmYXF5d3doZHd5Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzM3MDY2NjgsImV4cCI6MjA0OTI4MjY2OH0.St4GG0BWQfQlojlbGimBQyRagkl3FN73CyYsgeMa5bs')

export default function Calendar() {

  const [session, setSession] = useState(null)
  const [events, setEvents] = useState([]);

  useEffect(() => {
    getEvents();
  }, []);

  async function getEvents() {
    var date = new Date();
    date.setDate(date.getDate() - 1);
    const { data } = await supabase.from("economic-calendar").select().gt('date', date.toJSON()).order('date', { ascending: true });
    let mainEvets = [];
    for (const e of data){
      let obj = {...e};
        //https://kaitori-test-batch.s3.ap-northeast-1.amazonaws.com/report/2024-12-16T16-30-00.svg
        obj.img = "https://kaitori-test-batch.s3.ap-northeast-1.amazonaws.com/report/" + e.id + '.svg';
        mainEvets.push(obj)
    }
    setEvents(mainEvets);
  }

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
    })

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session)
    })

    return () => subscription.unsubscribe()
  }, [])

  if (!session) {
    return (<Auth supabaseClient={supabase} appearance={{ theme: ThemeSupa }} />)
  }
  else {
    return (
      <Table celled>
      <TableHeader>
        <TableRow>
          <TableHeaderCell>id</TableHeaderCell>
          <TableHeaderCell>title</TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell>(tokyo)</TableHeaderCell>
          <TableHeaderCell>()</TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell>10</TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
          <TableHeaderCell></TableHeaderCell>
        </TableRow>
      </TableHeader>
      <TableBody>
        {events.map((event) => (
          <TableRow key={event.id}>
            <TableCell>
            {event.id}
            </TableCell>
            <TableCell>
            {event.title}
            <Flag name={event.country.toLowerCase()} />
            </TableCell>
            <TableCell>
            <Rating icon='star' defaultRating={event.importance + 2}  maxRating={event.importance + 2} disabled/>
            </TableCell>
            <TableCell>
            {new Date(event.date).toLocaleDateString('ja-JP',{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric',
            })}
            </TableCell>
            <TableCell>
            {event.updated_at > event.date ? (new Date(event.updated_at) - new Date(event.date))/ 1000 : ''}
            </TableCell>
            <TableCell>
            {event.actual}
            </TableCell>
            <TableCell 
            positive={event.actual != null && event.previous != null && event.actual - event.previous > 0}
            negative={event.actual != null && event.previous != null && event.actual - event.previous < 0}
            >
            {event.actual === null || event.previous === null ? '' : (event.actual - event.previous).toFixed(3)}
            {event.actual != null && event.previous != null && event.actual - event.previous > 0 && <Icon name='arrow up'/>}
            {event.actual != null && event.previous != null && event.actual - event.previous < 0 && <Icon name='arrow down'/>}
            </TableCell>
            <TableCell
            positive={event.actual != null && event.forecast != null && event.actual - event.forecast > 0}
            negative={event.actual != null && event.forecast != null && event.actual - event.forecast < 0}
            >
            {event.actual === null || event.forecast === null ? '' : (event.actual - event.forecast).toFixed(3)}
            {event.actual != null && event.forecast != null && event.actual - event.forecast > 0 && <Icon name='arrow up'/>}
            {event.actual != null && event.forecast != null && event.actual - event.forecast < 0 && <Icon name='arrow down'/>}
            </TableCell>

            <TableCell>
            
            <Image src={event.img} size='massive'
            onError={({ currentTarget }) => {
              currentTarget.onerror = null; // prevents looping
              currentTarget.style.display='none';
            }}/>
            </TableCell>

            <TableCell>
            {event.previous}
            </TableCell>
            <TableCell>
            {event.forecast}
            </TableCell>
          </TableRow>
        ))}
        </TableBody>
      </Table>
    );
  }
}