import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import Calendar from './calendar';
import News from './news';
import Sns from './sns';
import reportWebVitals from './reportWebVitals';
import { TabPane, MenuItem, Label, Tab } from 'semantic-ui-react'

const panes = [
  {
    menuItem: { key: 'calendar', icon: 'calendar alternate outline', content: 'calendar' },
    render: () => <TabPane><Calendar/></TabPane>,
  },
  {
    menuItem: { key: 'news', icon: 'newspaper', content: 'news' },
    render: () => <TabPane><News/></TabPane>,
  },
  {
    menuItem: { key: 'twitter', icon: 'twitter', content: 'twitter' },
    render: () => <TabPane><Sns/></TabPane>,
  },
]
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
    <Tab panes={panes} />
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
