import 'semantic-ui-css/semantic.min.css'

import { useState, useEffect } from 'react';
import { createClient } from "@supabase/supabase-js";
import { Auth } from '@supabase/auth-ui-react'
import { ThemeSupa } from '@supabase/auth-ui-shared'
import {
  TableRow,
  TableHeaderCell,
  TableHeader,
  TableCell,
  TableBody,
  HeaderContent,
  Header,
  Table,
  Rating,
  Image
} from 'semantic-ui-react'


const supabase = createClient('https://fnicvjrmmtfaqywwhdwy.supabase.co', 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6ImZuaWN2anJtbXRmYXF5d3doZHd5Iiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzM3MDY2NjgsImV4cCI6MjA0OTI4MjY2OH0.St4GG0BWQfQlojlbGimBQyRagkl3FN73CyYsgeMa5bs')

export default function Sns() {

  const [session, setSession] = useState(null)
  const [events, setEvents] = useState([]);

  useEffect(() => {
    getEvents();
  }, []);

  async function getEvents() {
    var date = new Date();
    date.setDate(date.getDate() - 3);
    const { data } = await supabase.from("economic-sns").select().gt('time', date.toJSON()).order('time', { ascending: false });
    let mainEvets = [];
    for (const e of data){
      let obj = {...e};
      mainEvets.push(obj)
    }
    setEvents(mainEvets);
  }

  useEffect(() => {
    supabase.auth.getSession().then(({ data: { session } }) => {
      setSession(session)
    })

    const {
      data: { subscription },
    } = supabase.auth.onAuthStateChange((_event, session) => {
      setSession(session)
    })

    return () => subscription.unsubscribe()
  }, [])

  if (!session) {
    return (<Auth supabaseClient={supabase} appearance={{ theme: ThemeSupa }} />)
  }
  else {
    return (
      <Table celled>
      <TableHeader>
        <TableRow>
          <TableHeaderCell>id</TableHeaderCell>
          <TableHeaderCell>(tokyo)</TableHeaderCell>
          <TableHeaderCell>name</TableHeaderCell>
          <TableHeaderCell>context</TableHeaderCell>
        </TableRow>
      </TableHeader>
      <TableBody>
        {events.map((event) => (
          <TableRow key={event.id}>
            <TableCell>
            {event.id}
            </TableCell>
           
            <TableCell>
            {new Date(event.time).toLocaleDateString('ja-JP',{
              year: 'numeric',
              month: 'numeric',
              day: 'numeric',
              hour: 'numeric',
              minute: 'numeric',
              second: 'numeric',
            })}
            </TableCell>
            <TableCell>
              <Header as='h4' image>
                <Image src={event.avatar} rounded size='mini' />
                <HeaderContent>
                  <a href={event.profile} target="_blank">{event.name}</a>
                </HeaderContent>
              </Header>
            </TableCell>
            <TableCell>
            {event.context}
            </TableCell>
          </TableRow>
        ))}
        </TableBody>
      </Table>
    );
  }
}